<template>
  <div class="wrap">

    <input type="text"  ref="searchInput" v-model="value" @blur="toBlur(),setCookie(),sGoods()" @focus="toBlur">

    <div class="keepOut" :class="{none : !show}">
      {{!value ? DEFAULT_MSG : ''}}
    </div>

    <div class="cookie" :class="{none : show}">
        <div v-for="(item,index) in cookie" :key="index" @click="setValue(item),sGoods()">{{item}}</div>
    </div>

  </div>
</template>
<script>
import store from '../pages/counter/store.js'
  export default {
      data(){
          return {
              DEFAULT_MSG: '搜索商品',
              value: '',
              show: true,
              cookie: []
          }
      },
      methods:{
          toBlur(){
              this.show = !this.show 
          },
          setValue(data){
              this.value = data
          },
          setCookie(){
              const index = this.cookie.length
              let set = 0
              this.cookie.map((item)=>{
                  if(item === this.value ){
                      set++
                  }
              })
              if(set === 0){
                this.$set(this.cookie,index,this.value)
              }
          },
          sGoods(){
              const data = this.value
              store.commit('setSkey',{ data })
              store.commit('setShow')
          }
      }
  }
</script>
<style lang="less" scoped>
  .wrap{
      display: flex;
      flex-flow: column nowrap;
      box-sizing: border-box;
      width: 100%;
      height: 80rpx;
      text-align: center;
      padding: 10rpx 20rpx;
      z-index: 9
  }
  input{
      height: 100%;
      width: 100%;
      background:rgba(255, 255, 255, 0);  
  }
  .keepOut{
      box-sizing: border-box;
      margin-top: -60rpx;
      height: 60rpx;
      width: 100%;
      padding-right: 20rpx;
      line-height: 60rpx;
      font-size: 24rpx;
      background: #eee;
      border-radius: 30rpx;  
  }
  .cookie{
      display: flex;
      flex-flow: column nowrap;
      div{
          height: 60rpx;
          background: rgba(153, 153, 153, 0.1);
          margin-top: 8rpx;
          border-radius: 30rpx
      }
  }
  .none{
      display: none
  }
</style>


